.cart-item{
    width: 100%;
    display: flex;
    height: 80px;
    margin-bottom: 15px;

    img{
        width: 40%;
    }
    .item-details{
        width: 60%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        padding: 10px 20px;

        .name{
            font-size: 16px;
        }
    }
}